$switch-label-outline: 1px dotted $jumbo;

// 30. Tables
// - - - - - - - - - - - - - - - - - - - - - - - - -

$include-html-table-classes: $include-html-classes;

// These control the background color for the table and even rows
$table-bg: $white;
